* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Open Sans, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  overflow: hidden;
}

@media (prefers-color-scheme: light) {
  body {
    color: #000;
    background-color: #fff;
  }

  .sidebar {
    background-color: #fff;
    border-right: 1px solid #e0e0e0;
  }

  .course-card {
    background-color: #f8f9fa;
    border: 1px solid #e0e0e0;
  }

  .course-card:hover {
    background-color: #e9ecef;
  }
}

@media (prefers-color-scheme: dark) {
  body {
    color: #d3d3d3;
    background-color: #1a1a1a;
  }

  .sidebar {
    background-color: #2d2d2d;
    border-right: 1px solid #444;
  }

  .course-card {
    background-color: #3a3a3a;
    border: 1px solid #555;
  }

  .course-card:hover {
    background-color: #4a4a4a;
  }

  a {
    color: #627d98;
  }

  a:visited {
    color: #616e7c;
  }
}

.header {
  z-index: 2000;
  backdrop-filter: blur(10px);
  background: #fffffff2;
  border-bottom: 1px solid #0000001a;
  padding: 10px 20px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

@media (prefers-color-scheme: dark) {
  .header {
    background: #1a1a1af2;
    border-bottom: 1px solid #ffffff1a;
  }
}

.header h1 {
  text-align: center;
  margin: 0 0 10px;
  font-size: 1.5em;
}

.controls {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.location-control, .time-control, .course-control {
  align-items: center;
  gap: 8px;
  display: flex;
}

.location-control label, .time-control label {
  white-space: nowrap;
  font-weight: 500;
}

#userLatLon {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 200px;
  padding: 6px 10px;
  font-size: 14px;
}

.time-slider-container {
  align-items: center;
  gap: 10px;
  display: flex;
}

#timeSlider {
  width: 150px;
}

#timeDisplay {
  min-width: 70px;
  font-weight: 500;
}

.main-content {
  height: 100vh;
  padding-top: 120px;
  display: flex;
}

#map {
  background-color: #f0f0f0;
  flex: 1;
  height: calc(100vh - 120px);
  min-height: 400px;
}

.leaflet-control-container, .leaflet-control-zoom, .leaflet-control-attribution {
  z-index: 1000 !important;
}

.sidebar {
  width: 350px;
  height: calc(100vh - 120px);
  padding: 20px;
  position: relative;
  overflow-y: auto;
}

.sidebar-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  display: flex;
}

.sidebar-header h3 {
  margin: 0;
  font-size: 1.2em;
}

.toggle-btn {
  cursor: pointer;
  background: none;
  border: none;
  width: 30px;
  height: 30px;
  padding: 0;
  font-size: 24px;
  line-height: 1;
  display: none;
}

.sort-controls {
  margin-bottom: 20px;
}

.sort-controls label {
  margin-bottom: 5px;
  font-weight: 500;
  display: block;
}

.sort-controls select {
  background-color: inherit;
  width: 100%;
  color: inherit;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 6px 10px;
}

.course-list {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.course-card {
  cursor: pointer;
  border-radius: 8px;
  padding: 15px;
  transition: background-color .2s;
}

.course-card:hover {
  transform: translateY(-1px);
}

.course-name {
  margin-bottom: 8px;
  font-size: 1.1em;
  font-weight: 600;
}

.course-details {
  justify-content: space-between;
  margin-bottom: 8px;
  font-size: .9em;
  display: flex;
}

.weather-score {
  text-align: center;
  border-radius: 6px;
  min-width: 50px;
  padding: 8px;
  font-size: 1.2em;
  font-weight: 700;
}

.score-excellent {
  color: #fff;
  background-color: #28a745;
}

.score-good {
  color: #fff;
  background-color: #6db33f;
}

.score-fair {
  color: #000;
  background-color: #ffc107;
}

.score-poor {
  color: #fff;
  background-color: #fd7e14;
}

.score-bad {
  color: #fff;
  background-color: #dc3545;
}

.loading {
  text-align: center;
  color: #666;
  padding: 40px 20px;
  font-style: italic;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  transition: background-color .2s;
}

button:hover {
  background-color: #0056b3;
}

.course-control button {
  margin-bottom: 0;
}

#courseCount {
  color: #666;
  font-size: .9em;
}

.sunset-info {
  color: #fff;
  z-index: 1000;
  background: #000000b3;
  border-radius: 4px;
  padding: 8px 12px;
  font-size: .9em;
  position: fixed;
  bottom: 10px;
  left: 20px;
}

footer {
  color: #fff;
  z-index: 1000;
  background: #000000b3;
  border-radius: 4px;
  padding: 8px 12px;
  font-size: .8em;
  position: fixed;
  bottom: 10px;
  right: 20px;
}

footer a {
  color: #87ceeb;
}

@media (width <= 768px) {
  body {
    height: auto !important;
    overflow: auto !important;
  }

  .header {
    padding: 10px;
  }

  .header h1 {
    margin-bottom: 8px;
    font-size: 1.3em;
  }

  .controls {
    flex-direction: column;
    gap: 10px;
  }

  .location-control, .time-control, .course-control {
    flex-direction: column;
    align-items: stretch;
    gap: 5px;
  }

  #userLatLon {
    width: 100%;
  }

  .time-slider-container {
    justify-content: space-between;
  }

  .main-content {
    padding-top: 0;
    overflow: visible;
    height: auto !important;
    display: block !important;
  }

  #map {
    touch-action: manipulation;
    width: 100%;
    margin-top: 140px;
    margin-bottom: 0;
    transition: height .3s;
    position: relative;
    flex: none !important;
    height: 40vh !important;
    min-height: auto !important;
    overflow: visible !important;
  }

  .sidebar {
    border-top: 1px solid #e0e0e0;
    border-right: none;
    width: 100%;
    min-height: 60vh;
    padding-bottom: 20px;
    position: relative;
    height: auto !important;
    overflow: visible !important;
  }

  .sidebar-header {
    background: inherit;
    z-index: 10;
    margin-bottom: 0;
    padding-bottom: 15px;
    position: sticky;
    top: 0;
  }

  .toggle-btn {
    display: block;
  }

  .leaflet-popup {
    max-width: 250px !important;
  }

  .popup-content {
    min-width: 180px !important;
    padding: 8px !important;
  }

  .leaflet-popup-content-wrapper {
    max-height: 80vh !important;
    overflow-y: auto !important;
  }

  .custom-popup .leaflet-popup-content-wrapper {
    z-index: 1000 !important;
    position: relative !important;
  }

  .leaflet-popup-tip-container {
    overflow: visible !important;
  }

  .leaflet-popup-content {
    max-height: 70vh !important;
    margin: 8px !important;
    overflow-y: auto !important;
  }

  .chart-container {
    display: none !important;
  }

  .popup-course-name {
    margin-bottom: 6px !important;
    font-size: 1em !important;
  }

  .popup-details {
    margin-bottom: 8px !important;
    font-size: .8em !important;
  }

  .weather-factors .factor {
    margin-bottom: 3px !important;
    font-size: .8em !important;
  }

  .sunset-info, footer {
    text-align: center;
    margin: 10px;
    position: relative;
    bottom: auto;
    left: auto;
    right: auto;
  }
}

@media (width <= 480px) {
  .main-content {
    padding-top: 200px;
  }

  #map, .sidebar {
    height: 50vh;
  }

  .course-card {
    padding: 10px;
  }

  .course-details {
    flex-direction: column;
    gap: 5px;
  }
}

.leaflet-popup-content-wrapper {
  border-radius: 8px;
}

.popup-content {
  min-width: 200px;
  padding: 10px;
}

.popup-course-name {
  margin-bottom: 8px;
  font-size: 1.1em;
  font-weight: 600;
}

.popup-score {
  text-align: center;
  margin-bottom: 10px;
}

.popup-details {
  margin-bottom: 10px;
  font-size: .9em;
}

.chart-container {
  width: 150px;
  height: 150px;
  margin: 10px auto;
}

@media (prefers-color-scheme: dark) {
  .leaflet-popup-content-wrapper {
    color: #d3d3d3 !important;
    background-color: #1a1a1a !important;
  }

  .leaflet-popup-tip {
    background-color: #1a1a1a !important;
  }

  .popup-content {
    color: #d3d3d3 !important;
  }

  .popup-course-name {
    color: #fff !important;
  }

  .popup-details, .weather-factors .factor {
    color: #d3d3d3 !important;
  }
}

.about-content {
  max-width: 800px;
  margin: 140px auto 60px;
  padding: 0 20px;
  line-height: 1.6;
}

.about-section {
  background-color: #f8f9fa80;
  border: 1px solid #0000001a;
  border-radius: 8px;
  margin-bottom: 2rem;
  padding: 1.5rem;
}

.about-section h2 {
  color: #007bff;
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 1.4em;
}

.about-section p {
  margin-bottom: 1rem;
}

.about-section ul, .about-section ol {
  margin-bottom: 1rem;
  margin-left: 1.5rem;
}

.about-section li {
  margin-bottom: .5rem;
}

@media (prefers-color-scheme: dark) {
  .about-section {
    background-color: #2d2d2d80;
    border: 1px solid #ffffff1a;
  }

  .about-section h2 {
    color: #64c8ff;
  }
}

@media (width <= 768px) {
  .about-content {
    margin-top: 200px;
    padding: 0 15px;
  }

  .about-section {
    padding: 1rem;
  }

  .about-section h2 {
    font-size: 1.2em;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.course-card {
  animation: .3s ease-out fadeIn;
}

#timeSlider {
  appearance: none;
  background: #ddd;
  border-radius: 3px;
  outline: none;
  height: 6px;
}

#timeSlider::-webkit-slider-thumb {
  appearance: none;
  cursor: pointer;
  background: #007bff;
  border-radius: 50%;
  width: 18px;
  height: 18px;
}

#timeSlider::-moz-range-thumb {
  cursor: pointer;
  background: #007bff;
  border: none;
  border-radius: 50%;
  width: 18px;
  height: 18px;
}

.rating-form {
  background: #fffffff2;
  border-top: 1px solid #e0e0e0;
  border-radius: 8px;
  margin-top: 15px;
  padding: 15px;
}

@media (prefers-color-scheme: dark) {
  .rating-form {
    background: #2d2d2df2;
    border-top: 1px solid #444;
  }
}

.rating-form h4 {
  color: #333;
  margin: 0 0 12px;
  font-size: 1.1em;
}

@media (prefers-color-scheme: dark) {
  .rating-form h4 {
    color: #ddd;
  }
}

.rating-row {
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  display: flex;
}

.rating-label {
  color: #555;
  min-width: 80px;
  font-size: .9em;
  font-weight: 500;
}

@media (prefers-color-scheme: dark) {
  .rating-label {
    color: #ccc;
  }
}

.star-rating {
  align-items: center;
  gap: 2px;
  display: flex;
}

.star {
  color: #ddd;
  cursor: pointer;
  user-select: none;
  font-size: 18px;
  transition: color .2s;
}

.star:hover, .star.active {
  color: gold;
}

.star:hover ~ .star {
  color: #ddd;
}

.conditions-input {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  margin: 8px 0;
  padding: 8px;
  font-family: inherit;
  font-size: .9em;
}

@media (prefers-color-scheme: dark) {
  .conditions-input {
    color: #ddd;
    background: #3a3a3a;
    border: 1px solid #555;
  }
}

.conditions-input::placeholder {
  color: #999;
}

.user-id-input {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  margin: 8px 0;
  padding: 8px;
  font-family: inherit;
  font-size: .9em;
}

@media (prefers-color-scheme: dark) {
  .user-id-input {
    color: #ddd;
    background: #3a3a3a;
    border: 1px solid #555;
  }
}

.rating-form-buttons {
  gap: 8px;
  margin-top: 12px;
  display: flex;
}

.rate-button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 6px 12px;
  font-size: .8em;
  font-weight: 500;
  transition: background-color .2s;
}

.rate-button.primary {
  color: #fff;
  background-color: #007bff;
}

.rate-button.primary:hover {
  background-color: #0056b3;
}

.rate-button.secondary {
  color: #fff;
  background-color: #6c757d;
}

.rate-button.secondary:hover {
  background-color: #545b62;
}

.rate-button:disabled {
  opacity: .6;
  cursor: not-allowed;
}

.rating-message {
  border-radius: 4px;
  margin: 8px 0;
  padding: 8px 12px;
  font-size: .9em;
  font-weight: 500;
}

.rating-message.success {
  color: #155724;
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
}

.rating-message.error {
  color: #721c24;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
}

@media (prefers-color-scheme: dark) {
  .rating-message.success {
    color: #a3d9a5;
    background-color: #1e4f25;
    border: 1px solid #2d6a35;
  }

  .rating-message.error {
    color: #d9a3a3;
    background-color: #4f1c1c;
    border: 1px solid #6a2d2d;
  }
}
